import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';

export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			type1: ['点击选择', '类型1', '类型2'],
			type1Index: 0,
			type2: ['点击选择', '类别1', '类别2'],
			type2Index: 0,
			date: '点击选择',
			locationAddress: '选择地点',
			mark: '',
			dates: ''
		};
	},
	onReady() {
		var dateee = new Date().toJSON();
		this.dates = new Date(+new Date(dateee) + 8 * 3600 * 1000)
			.toISOString()
			.replace(/T/g, ' ')
			.replace(/\.[\d]{3}Z/, '');
	},

	methods: {
		updataJump() {
			console.log('上报提交');
		},
		ontrueGetList() {
			console.log('加载了第三个页面，可以把网络请求放这里');
		},
		sizeTypeChange(e) {
			this.type1Index = parseInt(e.detail.value);
		},
		sizeType2Change(e) {
			this.type2Index = parseInt(e.detail.value);
		},
		bindDateChange: function(e) {
			this.date = e.target.value;
		},

		chooseLocation: function() {
			uni.chooseLocation({
				success: res => {
					console.log(res.name);
					console.log(res.address);
					// this.location = formatLocation(res.longitude, res.latitude),
					this.locationAddress = res.name;
				}
			});
		},
		showPicker() {
			this.$refs.date1.show();
		},
		onConfirm(res, type) {
			this.date = res.result;
			console.log(res.result);
		}
	}
};
